
import axios from "axios";
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Flexbox,
FlexboxItem,
ViewBox,
Group,
Cell,
XButton,
XDialog,
XTextarea
},
props: ["userId", "Id", "name", "departmentId", "totalscore"],
data() {
return {
currItem: null,
rejectReason: "",
showRejectDialog: false,
list: []
};
},
computed: {
userName() {
return decodeURIComponent(this.name);
}
},
mounted() {
this.getlist();
},
methods: {
getlist() {
axios({
method: "get",
url: "pavantgrade/getList",
params: {
userId: this.userId
}
})
.then(res => {
console.log(res);
this.list = res.data;
this.list.forEach(item => {
item.memo = (item.memo && item.memo.split(",")) || [];
});
})
.catch(function(error) {
console.log(error);
});
},
auditReject(item) {
this.currItem = item;
this.showRejectDialog = true;
},
auditResolve(item) {
axios({
method: "post",
url: "pavantgrade/examineOK",
params: {
id: item.id
}
})
.then(res => {
this.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "top"
});
this.rejectReason = "";
})
.catch(function(error) {
console.log(error);
});
},
dialogConfirm() {
axios({
method: "post",
url: "pavantgrade/examineNo",
params: {
id: this.currItem.id,
rejectReson: this.rejectReason
}
})
.then(res => {
this.$vux.toast.show({
text: res.data.msg,
type: "text",
position: "top"
});
this.rejectReason = "";
})
.catch(function(error) {
console.log(error);
});
this.showRejectDialog = false;
},
dialogCancel() {
this.currItem = null;
this.rejectReason = "";
this.showRejectDialog = false;
}
}
};



